@charset "UTF-8";
@import "../utility/variable";

.sec-tokushoho {
    padding: 120px 0 100px;
    .ttl-page {
        font-size: 23px;
        font-weight: bold;
        margin-bottom: 40px;
    }
    .inner {
        max-width: 950px;
        margin: 0 auto;
        .bx {
            margin-bottom: 40px;
            &:last-child {
                margin-bottom: 0;
            }
            .ttl {
                font-size: 16px;
                letter-spacing: 0.1em;
                line-height: 1.4em;
                padding: 15px;
                background: #efefef;
                margin-bottom: 15px;
            }
            .txt {
                font-size: 14px;
                letter-spacing: 0.1em;
                line-height: 1.8em;
                a {
                    color: #000;
                    text-decoration: underline;
                }
            }
            .img {
                width: 100%;
                max-width: 600px;
                margin: 20px 0;
                display: block;
            }
            table {
                margin-bottom: 20px;
                th {
                    font-size: 14px;
                    vertical-align: top;
                }
                td {
                    font-size: 14px;
                    vertical-align: top;
                }
                .tar {
                    text-align: right;
                }
            }
            &.table01 {
                table {
                    width: 100%;
                    th {
                        padding-left: 0;
                        padding-right: 0;
                    }
                    td {
                        padding-left: 5px;
                        padding-right: 5px;
                    }
                }
            }
            &.table02 {
                table {
                    width: 50%;
                }
            }
        }
    }
    @media only screen and (max-width: 767px) {
        padding: 80px 0 70px;
        .ttl-page {
            font-size: 21px;
            margin-bottom: 30px;
        }
        .inner {
            .bx {
                margin-bottom: 30px;
                .ttl {
                    font-size: 14px;
                }
                .txt {
                    font-size: 12px;
                }
                &.table02 {
                    table {
                        width: 100%;
                        th:first-child,
                        td:first-child {
                            width: 60%;
                        }
                    }
                }
            }
        }
    }
}
