.sec-tokushoho {
  padding: 120px 0 100px;
}
.sec-tokushoho .ttl-page {
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 40px;
}
.sec-tokushoho .inner {
  max-width: 950px;
  margin: 0 auto;
}
.sec-tokushoho .inner .bx {
  margin-bottom: 40px;
}
.sec-tokushoho .inner .bx:last-child {
  margin-bottom: 0;
}
.sec-tokushoho .inner .bx .ttl {
  font-size: 16px;
  letter-spacing: 0.1em;
  line-height: 1.4em;
  padding: 15px;
  background: #efefef;
  margin-bottom: 15px;
}
.sec-tokushoho .inner .bx .txt {
  font-size: 14px;
  letter-spacing: 0.1em;
  line-height: 1.8em;
}
.sec-tokushoho .inner .bx .txt a {
  color: #000;
  text-decoration: underline;
}
.sec-tokushoho .inner .bx .img {
  width: 100%;
  max-width: 600px;
  margin: 20px 0;
  display: block;
}
.sec-tokushoho .inner .bx table {
  margin-bottom: 20px;
}
.sec-tokushoho .inner .bx table th {
  font-size: 14px;
  vertical-align: top;
}
.sec-tokushoho .inner .bx table td {
  font-size: 14px;
  vertical-align: top;
}
.sec-tokushoho .inner .bx table .tar {
  text-align: right;
}
.sec-tokushoho .inner .bx.table01 table {
  width: 100%;
}
.sec-tokushoho .inner .bx.table01 table th {
  padding-left: 0;
  padding-right: 0;
}
.sec-tokushoho .inner .bx.table01 table td {
  padding-left: 5px;
  padding-right: 5px;
}
.sec-tokushoho .inner .bx.table02 table {
  width: 50%;
}
@media only screen and (max-width: 767px) {
  .sec-tokushoho {
    padding: 80px 0 70px;
  }
  .sec-tokushoho .ttl-page {
    font-size: 21px;
    margin-bottom: 30px;
  }
  .sec-tokushoho .inner .bx {
    margin-bottom: 30px;
  }
  .sec-tokushoho .inner .bx .ttl {
    font-size: 14px;
  }
  .sec-tokushoho .inner .bx .txt {
    font-size: 12px;
  }
  .sec-tokushoho .inner .bx.table02 table {
    width: 100%;
  }
  .sec-tokushoho .inner .bx.table02 table th:first-child,
.sec-tokushoho .inner .bx.table02 table td:first-child {
    width: 60%;
  }
}